import React from 'react';
import { AutoPatternsTable } from '../AutoPatternsTable';
import { CollectionPageConfig } from '../../types';
import { AutoPatternsGrid } from '../AutoPatternsGrid';
import { AutoPatternsTableGridSwitch } from '../AutoPatternsTableGridSwitch/AutoPatternsTableGridSwitch';
import { SchemaProvider } from '../../providers';
import { SkeletonCollection } from './SkeletonCollection';

export interface AutoPatternsCollectionPageContentProps {
  configuration: CollectionPageConfig;
}

export const AutoPatternsCollectionPageContent = ({
  configuration,
}: AutoPatternsCollectionPageContentProps) => {
  const { components } = configuration;
  return (
    <>
      {components
        .map((component, index) => {
          const { layout, ...sharedConfig } = component;

          if (!layout?.length) {
            return null;
          }

          const tableConfig = layout.find(
            (item) => item.type === 'Table',
          )?.table;
          const gridConfig = layout.find((item) => item.type === 'Grid')?.grid;
          let componentType: 'Table' | 'Grid' = 'Table';

          let componentToRender = null;

          if (tableConfig && gridConfig) {
            const config = {
              ...sharedConfig,
              ...tableConfig,
              ...gridConfig,
            };
            componentToRender = (
              <AutoPatternsTableGridSwitch configuration={config} />
            );
          } else if (tableConfig) {
            const config = { ...sharedConfig, ...tableConfig };
            componentToRender = <AutoPatternsTable configuration={config} />;
          } else if (gridConfig) {
            componentType = 'Grid';
            const config = { ...sharedConfig, ...gridConfig };
            componentToRender = <AutoPatternsGrid configuration={config} />;
          }

          return componentToRender ? (
            <SchemaProvider
              key={`${sharedConfig.collection.collectionId}-${index}`}
              collection={sharedConfig.collection}
              skeleton={<SkeletonCollection componentType={componentType} />}
            >
              {componentToRender}
            </SchemaProvider>
          ) : null;
        })
        .filter(Boolean)}
    </>
  );
};
